</template>



<script>
export default {
  data() {
    return {
      country: "",
      category: "",
      number: "",
      modalOpen: false,
      warn: false,
      loading: true,
      showCoun: false,
      showCat: false,
      showNum: false,
      showDat: false,
      showHot: false,
      showTax: false,
      path: "",
      mobileModal: false,
    };
  },
  methods: {
    openModal() {
      if (window.innerWidth >= 900) {
        if (this.country != "" && this.number != "" && this.category != "") {
          this.modalOpen = true;
          this.warn = false;
          setTimeout(() => {
            this.showCoun = true;
          }, 500);
          setTimeout(() => {
            this.showCat = true;
          }, 1500);
          setTimeout(() => {
            this.showNum = true;
          }, 2500);
          setTimeout(() => {
            this.showDat = true;
          }, 3000);
          setTimeout(() => {
            this.showHot = true;
          }, 3500);
          setTimeout(() => {
            this.showTax = true;
          }, 4000);
          setTimeout(() => {
            this.loading = false;
            this.path = "/packages";
            document.querySelector(".home-next").style.background = "#3685fb";
          }, 5000);
        } else {
          this.warn = true;
          setTimeout(() => {
            this.warn = false;
          }, 3000);
        }
      } else if (window.innerWidth < 900) {
        if (this.country != "" && this.number != "" && this.category != "") {
          this.mobileModal = true;
          document.querySelector(".mobile__modal-next").style.background =
            "gray";
          this.warn = false;
          setTimeout(() => {
            this.showCoun = true;
          }, 500);
          setTimeout(() => {
            this.showCat = true;
          }, 1500);
          setTimeout(() => {
            this.showNum = true;
          }, 2500);
          setTimeout(() => {
            this.loading = false;
            this.path = "/packages";
            document.querySelector(".mobile__modal-next").style.background =
              "#3685fb";
          }, 3000);
        } else {
          this.warn = true;
          setTimeout(() => {
            this.warn = false;
          }, 3000);
        }
      }
    },
    closeModal() {
      if (window.innerWidth >= 900 && this.loading == !true) {
        this.modalOpen = false;
        this.country = "";
        this.number = "";
        this.category = "";
        this.showCoun = false;
        this.showCat = false;
        this.showNum = false;
        this.showDat = false;
        this.showHot = false;
        this.showTax = false;
        this.loading = true;
        this.path = "";
        document.querySelector(".home-next").style.background = "gray";
      } else if (window.innerWidth < 900 && this.loading == !true) {
        this.mobileModal = false;
        setTimeout(() => {
          this.country = "";
          this.number = "";
          this.category = "";
          this.loading = true;
          this.path = "";
          document.querySelector(".mobile__modal-next").style.background =
            "gray";
        }, 1000);
      }
    },
    goToPlanner() {
      const element = document.querySelector(".planner");
      element.scrollIntoView({ block: "center" });
      setTimeout(() => {
        document.querySelector(".planner").style.border = "3px solid #3685fb";
      }, 800);
      setTimeout(() => {
        document.querySelector(".planner").style.border = "3px solid #f8f8f8";
      }, 2000);
    },
    packageUp() {
      if (this.path != "") {
        setTimeout(() => {
          window.scrollTo(0, 0);
        }, 100);
      }
    },
  },
};
</script>

