</template>

<script>
export default {
  props: {
    links: {
      typeof: Array,
    },
  },
  methods: {
    timeUp() {
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    },
  },
};
</script>

<style>
