<template>
  <div name="reg" v-if="reg">
    <Reg @closeReg="closeReg" />
  </div>

  <div v-else>
    <Header :links="links" @openReg="openReg" />

    <router-view v-slot="{ Component }">
      <transition name="fade" mode="out-in">
        <component :is="Component" />
      </transition>
    </router-view>

    <Contact />
    <Up @up="up" />
    <Footer :links="links" />
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import Contact from "@/components/Contact.vue";
import Up from "@/components/Up.vue";
import Reg from "@/components/Reg.vue";

export default {
  components: {
    Header,
    Footer,
    Contact,
    Up,
    Reg,
  },
  methods: {
    up() {
      window.scrollTo(0, 0);
    },
    openReg() {
      this.reg = true;
    },
    closeReg() {
      this.reg = false;
    },
  },
  data() {
    return {
      reg: false,
      links: [
        { title: "Home", url: "/" },
        { title: "About", url: "/about" },
        { title: "Packages", url: "/packages" },
      ],
    };
  },
};
</script>

<style>
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 500ms;
}

.reg-enter-from,
.reg-leave-to {
  opacity: 0;
}

.reg-enter-to,
.reg-leave-from {
  opacity: 1;
}

.reg-enter-active,
.reg-leave-active {
  transition: opacity 500ms;
}

::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #3685fb;
  border-radius: 10px;
}
</style>